import { Route, RouteProps, Redirect } from 'react-router-dom';
import _isEmpty from 'lodash.isempty';
import userPermission from '../../hooks/userPermission';
import { MAINTENANCE_PATHS } from '../../routes/path';

interface RouteWrapperProps extends RouteProps {
	component: any;
	action: string;
	objectTarget: string;
}

const RouteWrapper = (props: RouteWrapperProps) => {
	const { component: Component, action, objectTarget, ...restOfProps } = props;
	const { permissions, userCan } = userPermission();
	/**
	 * We need check empty permission list, and allow all route on first time because we want to wait AclContext call api
	 */
	const isDenied = _isEmpty(permissions) && !userCan(objectTarget)(action);

	return (
		<Route
			exact
			{...restOfProps}
			render={props =>
				isDenied ? (
					<Redirect to={MAINTENANCE_PATHS.FORBIDDEN} />
				) : (
					<Component {...props} />
				)
			}
		/>
	);
};

export default RouteWrapper;
